#root{
  background-color: #f2f0fd;
}
.emptyCart {
  margin: auto;
  text-align: center;
  padding: 10vmax;
  height: 50vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.emptyCart > svg {
  font-size: 5vmax;
  color: tomato;
}
.emptyCart > p {
  font-size: 2vmax;
}
.emptyCart > a {
  background-color: rgb(51, 51, 51);
  color: white;
  border: none;
  padding: 1vmax 3vmax;
  cursor: pointer;
  font: 400 1vmax "Roboto";
  text-decoration: none;
}
.cartPage{
    
    padding: 5vmax;
}
.header{
    background-color: #611c66;
    width: 90%;
    box-sizing: border-box;
    margin: auto;
    color: white;
    display: grid;
    grid-template-columns: 4fr 1fr 1fr;
    font: 500 1vmax "Poppins";
  }
  .header > p {
    margin: 10px;
  }
  .header > p:last-child {
    text-align: end;
  }

  .cartContainer {
    width: 90%;
    margin: auto;
    display: grid;
    grid-template-columns: 4fr 1fr 1fr;
  }

  .cartInput {
    display: flex;
    align-items: center;
    height: 8vmax;
  }
  .cartInput > button {
    border: none;
    background-color: rgba(0, 0, 0, 0.616);
    padding: 0.5vmax;
    cursor: pointer;
    color: white;
    transition: all 0.5s;
  }
  .cartInput > button:hover {
    background-color: rgba(0, 0, 0, 0.767);
  }

  .cartInput > input {
    border: none;
    padding: 0.5vmax;
    width: 2.5vmax;
    margin-left: 10px;
  
    /* width: 1vmax; */
    text-align: center;
    outline: none;
    font: 400 0.8vmax "Roboto";
    color: rgba(0, 0, 0, 0.74);
  }
  .cartSubtotal {
    display: flex;
    padding: 0.5vmax;
    height: 8vmax;
    align-items: center;
    box-sizing: border-box;
    font: 300 1vmax cursive;
    justify-content: flex-end;
    color: rgba(0, 0, 0, 0.753);
  }

  .cartGrossProfit {
    display: grid;
    grid-template-columns: 2fr 1.2fr;
  }
  
  .cartGrossProfitBox {
    border-top: 3px solid #611c66;
    margin: 1vmax 4vmax;
    box-sizing: border-box;
    padding: 2vmax 0;
    font: 300 1vmax "Poppins";
    display: flex;
    justify-content: space-between;
  }
  
  .checkOutBtn {
    display: flex;
    justify-content: flex-end;
  }
  .checkOutBtn > button {
    background-color: #611c66;
    color: white;
    border: none;
    padding: 0.8vmax 4vmax;
    width: 50%;
    font: 300 0.8vmax "Poppins";
    margin: 1vmax 4vmax;
    cursor: pointer;
    border-radius: 30px;
  }
  .checkOutBtn > button:hover{
    background-color: tomato;
    transition: 0.5s;
  }
  
  @media screen and (max-width: 600px) {
    .cartPage {
        padding: 0;
        min-height: 60vh;
      }
      .header {
        width: 100%;
        font: 300 1.7vmax "Roboto";
        grid-template-columns: 3fr 1fr 1fr;
      }
      .cartContainer {
        width: 100%;
        grid-template-columns: 3fr 1fr 1fr;
      }
    
      .cartInput {
        height: 20vmax;
      }
    
      .cartInput > button {
        padding: 1.5vmax;
      }
    
      .cartInput > input {
        width: 2vmax;
        padding: 1.5vmax;
        font: 400 1.8vmax "Poppins";
      }
    
      .cartSubtotal {
        padding: 1.5vmax;
        height: 20vmax;
        font: 300 2vmax "Poppins";
      }
    
      .cartGrossProfit {
        display: grid;
        grid-template-columns: 0fr 2fr;
      }
    
      .cartGrossProfitBox {
        padding: 2vmax;
        font: 300 2vmax "Poppins";
      }
    
      .checkOutBtn > button {
        padding: 2vmax 4vmax;
        width: 100%;
        font: 300 2vmax "Poppins";
      }
  }