.CartItemCard {
    display: flex;
    padding: 1vmax;
    height: 10max;
    align-items: flex-start;
    box-sizing: border-box;
  }
  .CartItemCard > img {
    width:10vmax;
    cursor: pointer;
   
  }
  
  .CartItemCard > div {
    display: flex;
    margin: 0.3vmax 1vmax;
    flex-direction: column;
  }
  
  .CartItemCard > div > a {
    font: 300 1.5vmax Poppins;
    color: rgba(24, 24, 24, 0.815);
    text-decoration: none;
  }
  
  .CartItemCard > div > span {
    font: 300 1.3vmax "Poppins";
    color: rgba(24, 24, 24, 0.815);
  }
  
  .CartItemCard > div > p {
    color: tomato;
    font: 100 0.8vmax "Poppins";
    cursor: pointer;
  }
  .CartItemCard > div > p:hover {
    color: black;
    transition: 0.5s;
    
  }
  
  @media screen and (max-width: 600px) {
    .CartItemCard {
      padding: 3vmax;
      height: 25vmax;
    }
    .CartItemCard > img {
      width: 10vmax;
    }
  
    .CartItemCard > div {
      margin: 1vmax 2vmax;
    }
  
    .CartItemCard > div > a {
      font: 300 2vmax cursive;
    }
  
    .CartItemCard > div > span {
      font: 300 1.9vmax "Poppins";
    }
  
    .CartItemCard > div > p {
      font: 100 1.8vmax "Poppins";
    }
  }
  