h1 {
    margin: 2vmax auto;
    width: 15vw;
    border-bottom: 1px solid rgba(0, 0, 0, 0.171);
    padding: 2vmax ;
    color: rgba(0, 0, 0, 0.678);
    font: 500 1.5vmax "poppins";
    text-align: center;
  }
  .body {
    display: flex;
    justify-content: center;
    padding: 0;
  }
  


  /* for pagination */

  .pagi {
    display: flex;
    justify-content: center;
    margin: 6vmax;
  }
  .pagination {
    display: flex;
    justify-content: center;
    padding: 0;
  }
   .page-item {
    background-color: rgb(255, 255, 255);
    list-style: none;
    /* border: 1px solid rgba(0, 0, 0, 0.178); */
    padding: 1vmax 0.7vmax;
    transition: all 0.3s;
    cursor: pointer;
  }
  .page-item:first-child {
    border-radius: 5px 0 0 5px;
  }
  
  .page-item:last-child {
    border-radius: 0 5px 5px 0;
  }
  .page-link {
    text-decoration: none;
    font: 300 0.9vmax "Roboto";
    color: rgb(80, 80, 80);
    transition: all 0.3s;
  }
  
   .page-item:hover {
    background-color: rgb(230, 230, 230);
  }
  
  .page-item:hover .page-link {
    color: rgb(0, 0, 0);
  }
   
  .pageItemActive {
    background-color: tomato;
  } 
  
  .pageLinkActive {
    color: white;
  }   

  .filterbox {
    width: 12vmax;
    position: absolute;
    top: 11vmax;
    left: 2vmax;
  }
  .categoryBox {
    padding: 0%;
  }
  
  .category-link {
    list-style: none;
    color: rgba(0, 0, 0, 0.61);
    font: 400 1.1vmax "Roboto";
    margin: 1vmax;
    cursor: pointer;
    transition: all 0.5s;
  }
  .category-link:hover {
    color: tomato;
  }
  .visitAgain{
    margin: 2vmax auto;
    width: 15vw;
    /* border-bottom: 1px solid rgba(0, 0, 0, 0.171); */
    padding: 2vmax ;
    color: rgba(0, 0, 0, 0.678);
    font: 500 1.5vmax "Roboto";
    text-align: center;
  }

  
  @media screen and (max-width: 600px) {
    .filterbox {
    width: 20vmax;
    position: static;
    margin: auto;
    }
    .category-link{
      font:400 2.8vmax Roboto;
    }
  }